import React from "react";
import classes from "./Post.module.css";

const Post = (props) => {

  return (
    <div className={classes.item}>
      <img
        alt="noun"
        src="data:image/png;base64,"
      />
      {props.message}
      <div>
        <span>like {props.likesCount}</span>
      </div>
    </div>
  );
};
export default Post;
